      "There are many rental options around Fontainbleau and the south of Seine et marne. The area is well known for climbing and hiking. It is recommended to book accomodation as soon as possible.",
    "Accomodation.box12Paragraph":
      "This area is well known for climbing and hiking, it is recomended to book an accomodation as soon as possible",
    "Accomodation.box13Paragraph":
      "Web sites suggestions for accomodation in the area:",
    "Accomodation.box131Paragraph": " (near Nemours)",
    "Accomodation.box14Paragraph":
      "Web sites suggestions for a broader search:",
    "Accomodation.box2Title": "Campsites",
    "Accomodation.box2Paragraph":
      "There are several campsites around the forest of Fontainbleau. Some of the most popular campsites are:",
    "Accomodation.box21Paragraph": " (near Montigny-sur-loing)",
    "Accomodation.box22Paragraph": " (near Noisy-sur-cole)",
    "Accomodation.box23Paragraph": " (near Malesherbes)",
    "Accomodation.box24Paragraph": " (near Veneux-les-Sablons)",
    "Accomodation.box3Title": "Tourist office",
    "Accomodation.paragraph":
      "To get more information aboutavailable accomodation you can visit the tourist office websites of Fontainebleau, Melun and Nemours",
    "Accomodation.officeFontaineblau": "Tourist office of Fontainebleau",
    "Accomodation.officeMelun": "Tourist office of Melun",
    "Accomodation.officeNemours": "Tourist office of Nemours",
    "Home.title": "Home",
    "Home.bacgroundParagraph":
      "3 orienteering days in the forest of Fontainebleau and its surroundings",
    "Home.baliseSpTitle": "Sprint",
    "Home.baliseSpParagraph1": "21st",
    "Home.baliseSpParagraph2": "August",
    "Home.baliseMdTitle": "MD",
    "Home.baliseMdParagraph1": "22nd",
    "Home.baliseMdParagraph2": "August",
    "Home.baliseLdTitle": "LD",
    "Home.baliseLdParagraph1": "23rd",
    "Home.baliseLdParagraph2": "August",
    "Home.box1Title": "Information",
    "Home.box11Paragraph":
      "The national orienteering North West 2020 will take place between 21st - 23rd of August 2020 in the forest of Fontainebleau. This weekend consists of three races:",
    "Home.box121Paragraph":
      "Urban sprint on Friday 21st August afternoon in Chateau Landon",
    "Home.box122Paragraph":
      "Middle distance on Saterday 22nd August afternoon on the map 'Salamandre'",
    "Home.box123Paragraph":
      "Long distance on Sunday 23rd August morning on the map 'Franchard'",
    "Home.box13Paragraph":
      "The terrain for the urban sprint is a typical small vilage form south of Seine et Marne. Chateau Landon offers a wide range of environments that will make the orienteering challenging: small alleys, residential areas allowing higher speed as well as several parks.",
    "Home.box14Paragraph":
      "The terrain for the middle and long distance are typical Fontainbleau forests. There are a lot of rocky slopes and sandy hills which makes orienteering difficult. Between these more detailed areas the terrain offers a much better visibility as well as a good network of paths making the navigzation easier.",
    "Home.box15ParagraphTitle": "Nursery: ",
    "Home.box15Paragraph":
      "Due to COVID-19, we are not able to set up a nursery during these three days. For those with children, please specify in the registration that you as parents need a special arrangement for the starting time.",
    "Home.box16ParagraphTitle": "Refreshment bar: ",
    "Home.box16Paragraph":
      "During the 3 days event you will be able to pay by card at the refreshment bar",
    "Home.box17ParagraphTitle": "Registration: ",
    "Home.box171Paragraph": "Registration for races are now open on",
    "Home.box172Paragraph": "website until 7th augustt.",
    "Home.box2Title": "Forbidden area",
    "Home.box2Paragraph":
      "The area marked in red below is forbidden. Competitors are not allowed to enter the forbidden area before the competitions, otherwise they will be disqualified.",
